export const constants = {
    isTouch: "ontouchstart" in window ? function() {document.body.classList.add("touch"); return true;}() : false,
    window: $(window),
    body: $("body"),
    header: $(".header")
};

export function menuOpener() {
    $(".menu-opener").on("click", e => {
        e.preventDefault();
        this.body.toggleClass("menu-opened");
    });

    $(".main-menu [href=\"#involved\"]").on("click", e => {
        e.preventDefault();
        this.body.removeClass("menu-opened");

        if(!this.isTouch) {
            setTimeout(() => {
                $(".subscribe-form [type=\"email\"]").focus();
            }, 1000);
        }

        if($(".involve-holder").length > 0) {
            let parent = $(".involve-holder");

            $(".involve-holder").addClass("subscribe-opened");

            $(document).on("click", function(e) {
                if(
                    $(e.target).closest(parent).length === 0
                    && $(e.target).closest($(".menu")).length === 0
                ) {
                    parent.removeClass("subscribe-opened");
                    $(document).off("click");
                }
            });
        } else {
            let val = $(".subscribe-form").offset().top - this.window.height() / 2;
            $("html, body").animate({
                scrollTop: val
            }, 500);
        }

    });
}

export function scrollTo(section) {
    $(".scroll-to").on("click", (e) => {
        e.preventDefault();
        $("html, body").animate({
            scrollTop: $(section).offset().top - 30
        }, 1000);
    });
}

export function headerInverter() {
    const intro = $(".intro-block");
    const introEdge = intro.offset().top + intro.height();

    this.window.on("load scroll", () => {
        if(this.window.scrollTop() >= introEdge - 50) {
            this.header.addClass("inverted");
        } else {
            this.header.removeClass("inverted");
        }
    });
}

export function subscribeOpener() {
    const parent = $(".involve-holder");
    $(".subscribe-opener").on("click", e => {
        e.preventDefault();
        e.stopPropagation();

        if(!this.isTouch) {
            $(".subscribe-form [type=\"email\"]").focus();
        }

        parent.toggleClass("subscribe-opened");
        $(document).on("click", function(e) {
            if($(e.target).closest(parent).length === 0) {
                parent.removeClass("subscribe-opened");
                $(document).off("click");
            }
        });
    });

    $(".subscribe-closer").on("click", e => {
        e.preventDefault();
        e.stopPropagation();
        parent.removeClass("subscribe-opened");
        $(document).off("click");
    });
}

export function videoOpener() {
    const videoPopup = $(".video-popup");
    let video = document.querySelector(".video-popup video");

    this.body.on("click", ".video-play", e => {
        e.preventDefault();
        videoPopup.fadeIn();
        video.play();
    });
    $(".video-closer").on("click", e => {
        e.preventDefault();
        videoPopup.fadeOut();
        video.pause();
        setTimeout(() => {
            video.currentTime = 0;
        }, 500);
    });
}

export function infoSlides() {
    const slider = $(".info-slides");
    const total = $(".info-slides .slide").length;
    const line = $(".info-slides-line");
    const linkNext = $(".link-next a");
    const linkNext2 = $(".link-next2");
    const setLineWidth = (element, nextIndex, totalIndexes) => {
        let newWidth = (+nextIndex + 1) / +totalIndexes * 100;
        element.css({width: newWidth + "%"});
    };
    let index = 0;

    this.window.on("load resize", () => {
        if(this.window.width() >= 1025) {

            setLineWidth(line, 0, total);
            slider.on("beforeChange", function(event, slick, currentSlide, nextSlide) {
                setLineWidth(line, nextSlide, total);
                index = nextSlide;
                if(index < 1) {
                    linkNext.removeClass("disabled");
                } else {
                    linkNext.addClass("disabled");
                }
            });
            slider.on("init", function() {
                let video = document.querySelector(".video-holder video");
                video.play();
            });

            let isMoving = false;

            function navigate(event) {
                if(!isMoving) {
                    if(event.deltaY < 0 && index < total - 1) {
                        slider.slick("slickGoTo", index + 1);
                    }
                    if(event.deltaY > 0 && index > 0) {
                        slider.slick("slickGoTo", index - 1);
                    }
                    if(navigator.appVersion.indexOf("Mac")!=-1) {
                        console.log("isMac");
                        isMoving = true;
                        setTimeout(function() {
                            isMoving = false;
                        }, 2000);
                    }
                }
            }
            slider.on("mousewheel", navigate);

            linkNext.on("click", e => {
                e.preventDefault();
                linkNext.addClass("disabled");
                slider.slick("slickGoTo", 1);
            });
            linkNext2.on("click", e => {
                e.preventDefault();
                slider.slick("slickGoTo", 2);
            });

            if(!slider.hasClass("slick-initialized")) {
                slider.slick({
                    infinite: false,
                    arrows: false,
                    speed: 750,
                    responsive: [
                        {
                            breakpoint: 1025,
                            settings: "unslick"
                        }
                    ]
                });
                slider.slick("slickGoTo", 0);
            }
        } else {
            linkNext.removeClass("disabled");
            linkNext.off("click");
            linkNext.on("click", e => {
                e.preventDefault();
                let val = this.window.height();
                $("html, body").animate({
                    scrollTop: val
                }, 500);
            });
            linkNext2.on("click", e => {
                e.preventDefault();
                let val = $(".initiative-section").offset().top;
                $("html, body").animate({
                    scrollTop: val
                }, 500);
            });
        }
    });
}

export function partnersLetters() {
    $(".partners-table-holder .partner").each((index, item) => {
        $(item).find("span").html($(item).find("strong").html().charAt(0).toUpperCase());
    });
}

export function customScroll() {
    this.window.on("load resize", () => {
        if(this.window.width() >= 1025) {
            $(".custom-scroll").mCustomScrollbar();
        }
    });
}

export function partnersCarousel() {
    const $parent = $(".partners-block");
    const $slider = $parent.find($(".partners-grid"));

    this.window.on("load resize", () => {
        if(this.window.width() >= 1025) {
            if(!$slider.hasClass("slick-initialized")) {
                $slider.slick({
                    infinite: false,
                    slidesToShow: 5,
                    slidesToScroll: 1,
                    prevArrow: $parent.find($(".prev")),
                    nextArrow: $parent.find($(".next")),
                    responsive: [
                        {
                            breakpoint: 1025,
                            settings: "unslick"
                        }
                    ]
                });
            }
        }
    });
}


