export function dots() {
    if(this.window.width() < 1280 && this.isTouch) return;

    var canvas, ctx, center, windowSize;
    var circleNumber, posStart;
    var easeX, easeY;
    
    window.DOT_GAP = 26;
    window.SPHERE_RADIUS = 300;
    window.DOT_RADIUS_MAX = 2;
    window.SPEED = 0.15;
    window.BG_COLOR = "#7b00ff";
    window.DOT_COLOR = "#ffffff";
    window.FOLLOW_MOUSE = false;
    
    
    function init() {
        initDatGui();
        canvas = document.getElementById("canvas");
        ctx = canvas.getContext("2d");
        
        easeX = new Ease(0.5, 2, 60, 0);
        easeY = new Ease(0.5, 2, 60, 0);
        
        window.onresize = resize;
        canvas.onmousemove = moveCenter;
        
        resize();
        draw();
    }
    
    
    function initDatGui() {
        var gui = new dat.GUI();

        var folder1 = gui.addFolder("Parameters");
        var dotGap = folder1.add(window, "DOT_GAP", 10, 200).step(1).name("Dot gap");
        folder1.add(window, "SPHERE_RADIUS", 50, 500).step(1).name("Sphere radius");
        folder1.add(window, "DOT_RADIUS_MAX", 1, 200).step(1).name("Dot radius max");
        folder1.add(window, "SPEED", 0.01, 0.5).step(0.01).name("Speed");
        folder1.add(window, "FOLLOW_MOUSE").name("Follow mouse");
        
        var folder2 = gui.addFolder("Colors");
        folder2.addColor(window, "BG_COLOR").name("Background");
        folder2.addColor(window, "DOT_COLOR").name("Dots");
        
        dotGap.onChange( setDotParams.bind(this) );
        
        folder1.open();
        folder2.open();
    }
    
    
    function resize() {
        windowSize = {
            w : window.innerWidth,
            h : window.innerHeight
        };
        
        canvas.width = windowSize.w;
        canvas.height = windowSize.h;
        
        center = {
            x: windowSize.w / 2, 
            y: windowSize.h / 2
        };
        
        setDotParams();
    }
    
    
    function setDotParams() {
        circleNumber = {
            x: Math.floor(windowSize.w / DOT_GAP) + 2,
            y: Math.floor(windowSize.h / DOT_GAP) + 1
        };
        
        posStart = {
            x: Math.round( windowSize.w - (circleNumber.x - 1) * DOT_GAP ) / 2,
            y: Math.round( windowSize.h - (circleNumber.y - 1) * DOT_GAP ) / 2
        };
    }
    
    
    function draw() {
        requestAnimationFrame(draw);
        render();
    }
    
    
    function render() {
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        
        if (!FOLLOW_MOUSE)
            moveCenter(null);
        drawDots();
    }
    
    
    function moveCenter(e) {
        if (e === null) { // random motion
            easeX.update(SPEED);
            easeY.update(SPEED);
    
            center.x = easeX.value * windowSize.w;
            center.y = easeY.value * windowSize.h;
        }
        else { // follow mouse
            center.x = e.pageX;
            center.y = e.pageY;
        }
    }
    
    
    function drawDots() {
        var gapX, x, y, distance, alpha, radius;
        
        ctx.beginPath();
        ctx.fillStyle = BG_COLOR;
        ctx.rect(0, 0, windowSize.w, windowSize.h);
        ctx.fill();
        ctx.closePath();
        
        for (var i = 0; i < circleNumber.x; i++) {
            for (var j = 0; j < circleNumber.y; j++) {
                
                gapX = j % 2 === 0 ? - DOT_GAP / 2 : 0;
                x = posStart.x + gapX + i * DOT_GAP;
                y = posStart.y + j * DOT_GAP;
                
                distance = getDistance(x, y);
                
                if (distance <= SPHERE_RADIUS) {
                    alpha = getAlpha(distance);
                    radius = getRadius(alpha);
                    
                    ctx.save();
                    ctx.globalAlpha = alpha;
                    ctx.beginPath();
                    ctx.fillStyle = DOT_COLOR;
                    ctx.arc(x, y, radius, 0, 2 * Math.PI, false);
                    ctx.fill();
                    ctx.closePath();
                    ctx.restore();
                }
                
            }
        }
        
    }
    
    
    function getDistance(x, y) {
        var distance, distanceX, distanceY;
        
        distanceX = x - center.x;
        distanceY = y - center.y;
        distance = Math.sqrt( distanceX * distanceX + distanceY * distanceY );
        
        return distance;
    }
    
    
    function getAlpha(distance) {
        var alpha = 1 - (distance / SPHERE_RADIUS);
        
        return alpha;
    }
    
    
    function getRadius(alpha) {
        var radius = DOT_RADIUS_MAX * alpha;
        
        return radius;
    }
    
    
    function Ease(value, pow, duration, timeBegin ) {
        this.value = this.begin = this.end = value;
        this.pow = pow;
        this.maxDuration = duration;
        this.time = timeBegin;
        
        this.init();
    }
    
    
    Ease.prototype = {
        
        init : function() {
            this.begin = this.end;
            
            this.end = Math.random();
            
            this.time = 0;
            this.duration = Math.sqrt( Math.abs ( this.end - this.begin ) ) * this.maxDuration;
        },
        
        
        update : function(timeChange) {
            if (timeChange === undefined)
                timeChange = 1;
            var timeRatio = this.time / this.duration;
            
            if (timeRatio < 0.5)
                timeRatio = 0.5 * Math.pow(timeRatio * 2, this.pow);
            else
                timeRatio = 1 - 0.5 * Math.pow( (1 - timeRatio) * 2, this.pow );
            
            this.value = this.begin + timeRatio * (this.end - this.begin);
            this.time += timeChange;
            if(this.time > this.duration)
                this.init();
        }
        
    };
    
    window.onload = init;
}


